
  // @ts-ignore
  import Card from "../components/Card.vue"
  import {YoutubeVideoAPI} from "@/services/YoutubeVideoAPI";
  import Vue from "vue"
  import { FetchData, VideoItems } from '@/services/VideoTypings';
  import { Component } from 'vue-property-decorator'
  import moment from "moment"

  @Component({
      components: {
          Card
      }
  })
  export default class Home extends Vue {
    private videoController = new YoutubeVideoAPI("Immocraft");
    private prevToken?: string;
    private nextToken?: string;
      data() {
          return {
              channelData: null,
              videos: []
          }
      }
    private channelData?: FetchData<VideoItems>;
    private videos?: Array<VideoItems>;
    name = 'Home';
    private getYtLink(id: string) {
        return `https://youtube.com/watch?v=${id}` //https://www.youtube.com/watch?v=11gl3GYwDvY
    }
    private getTimestamp(iso: string) {
        return moment.utc(iso).local()
    }
    private getVideos() {
        return this.videos
    }

    async mounted(): Promise<void> {
      const data = this.videoController;
      this.channelData = await data.getVideoSearch();
      this.nextToken = this.channelData.nextPageToken;
      this.videos = this.channelData.items
    }

    async getMoreVideos(): Promise<void> {
      const data = this.videoController;
      data.videoSearchUrl = `${data.videoSearchUrl}&pageToken=${this.nextToken}`;
      const newVideos = await data.getVideoSearch();
      this.nextToken = newVideos.nextPageToken;
      newVideos.items.forEach((data: VideoItems) => {
        this.videos!.push(data)
      })
    }
}
