
















import Vue from "vue"
import {Component, Prop} from "vue-property-decorator"
  @Component
  export default class Card extends Vue {
      name = "Card";

      @Prop({ required: true })
      title?: string;
      @Prop({ required: true })
      link?: string;
      @Prop({ required: true })
      thumbnail_url?: string;
      @Prop({ required: true })
      timestamp_posted?: string;
      @Prop({ required: true })
      description?: string;
      @Prop({ required: true })
      channel_name?: string;
  }
