import Axios from "axios"
import {ChannelItems, FetchData, VideoItems} from "@/services/VideoTypings";

export class YoutubeVideoAPI {
    private axios = Axios.create();
    public videoUrl?: string;
    public videoSearchUrl?: string;
    public channelUrl?: string;

    constructor(channelId: string, maxResult: number = 10, apiKey: string = "AIzaSyDn7vPaM3GxiI9ORF4sl4ItRKrVeU-Kdzk") {
        this.videoUrl = `https://www.googleapis.com/youtube/v3/search?key=${apiKey}&channelId=${channelId}&part=snippet,id&order=date&maxResults=${maxResult}`;
        this.videoSearchUrl = `https://www.googleapis.com/youtube/v3/search?key=${apiKey}&part=snippet&order=date&maxResults=${maxResult}&q=${channelId}&order=date`;
        this.channelUrl = `https://www.googleapis.com/youtube/v3/channels?key=${apiKey}&id=${channelId}&part=snippet,contentDetails,statistics`
    }

    public async getVideos(): Promise<FetchData<VideoItems>> {
        let response = await this.axios.get<FetchData<VideoItems>>(this.videoUrl!);
        return response.data
    }
    
    public async getChannel(): Promise<FetchData<ChannelItems>> {
        let response = await this.axios.get<FetchData<ChannelItems>>(this.channelUrl!);
        return response.data
    }
    
    public async getVideoSearch(): Promise<FetchData<VideoItems>> {
        let response = await this.axios.get<FetchData<VideoItems>>(this.videoSearchUrl!);
        return response.data
    }
}
